<template>
    <div class="register-page input-border-none self-mt-header" :style="clientHeight">
		<header class="post-head">
            <a href="javascript:;" class="back" @click="backHistory"></a> 
            <span class="head-font">忘记密码</span>
        </header>
		<!-- <mt-header title="忘记密码" fixed>
            <router-link to="/" slot="left">
                <mt-button icon="back"></mt-button>
            </router-link>
        </mt-header> -->
		<div class="title-logo"></div>
        <div class="register-box">
            <el-form :model="registerForm" status-icon :rules="ruleRegister" ref="ruleRegister"  class="demo-ruleForm" @submit.prevent="goRegister">
				<div class="items-line">
					<img :src="loginPhone" alt="">
					<el-form-item label="" prop="mobile">
						<el-input type="text" v-model.trim="registerForm.mobile" auto-complete="off" placeholder="请输入手机号码" maxlength="11"></el-input>
					</el-form-item>
				</div>
				<div class="items-line">
					<img :src="registerCode" alt="">
					<el-input placeholder="请输入验证码" v-model.trim="registerForm.smsCode">
						<template slot="append" > 
							<span v-show="show" @click="getValidateCode">获取验证码</span> 
							<span v-show="!show">重新获取 {{count}} S</span>
						</template>
					</el-input>
				</div>
				<div class="items-line">
					<img :src="registerPwd" alt="">
					<el-form-item label="" prop="passWord">
                            <el-input type="password" v-model.trim="registerForm.passWord" auto-complete="off" placeholder="请输入密码"></el-input>
                        </el-form-item>
				</div>
				<div class="items-line">
					<img :src="registerPwd" alt="">
					<el-form-item label="" prop="checkPass">
                            <el-input type="password" v-model.trim="registerForm.checkPass" auto-complete="off" placeholder="请确认密码"></el-input>
                        </el-form-item>
				</div>
            </el-form>              
    	</div>
		<div class="zhanwei"></div>
		<div class="login-btn-box">
			<span @click="goRegister">确认</span>
		</div>
    </div>

</template>

<script>
import { Notify} from 'vant';
import loginTitle from "@/assets/qds-img/login-title.png";
import loginPhone from "@/assets/qds-img/login-phone.png";
import loginLock from "@/assets/qds-img/login-lock.png";
import loginCode from "@/assets/qds-img/login-code.png";
import registerCode from "@/assets/qds-img/register_code.png";
import registerPwd from "@/assets/qds-img/register_pwd.png";
export default {
    data() {
        return {
			globalApiUrl: domain.testUrl,       // 全局域名
			qdsGlobalApiUrl: domain.qdsDomain,       // 快招渠道商全局域名
            loginTitle: loginTitle,
			loginPhone: loginPhone,
			loginLock: loginLock,
			loginCode: loginCode,
			registerCode: registerCode,
			registerPwd: registerPwd,
			contentText:'',
			textShow: false,
            show: true,
            count: '',
            timer: null, 
            registerForm: {
                corpName: "",
                mobile: "",
                smsCode: "",
                passWord: "",
                checkPass: ""
            },
            ruleRegister: {
               
            },
            corpNameTipStyle: false,
            whetherRegister: 'exist',
            corpNameTipFont: '',
            loading: false,
            companyNameList: [],    // 搜索到的公司集合
            list: [{value: 0,label: '正在加载中'}], 
			checked: false,    // 是否同意用户协议
			clientHeight: {
				height: ""
			},
        };
    },
    created() {
		window.addEventListener('resize', this.getHeight);
		this.getHeight()
	},
	destroyed(){
        window.removeEventListener('resize', this.getHeight)
    },
    mounted() {},
    components: {
       Notify
    },
    methods: {
		 backHistory() {
            window.history.back(-1)
        },
		getHeight(){
            this.clientHeight.height = window.innerHeight - 0 + 'px';
        },
        getValidateCode() {   //获取验证码
            var that = this
            if(that.registerForm.mobile == '') {
				Notify({
                    message: '请输入手机号',
                    duration: 2000,
                    background: '#FF6262'
                });
            }else if(!that.registerForm.mobile.match(/^1[0-9]{10}$/)) {
				Notify({
                    message: '手机号格式不正确',
                    duration: 2000,
                    background: '#FF6262'
                });
            }else {
                that.axios(that.qdsGlobalApiUrl + 'notValidate/getforgetnSignCode', {
                    method: 'post',
                    params: {
                        mobile: that.registerForm.mobile
                    }
                })
                .then(function (response) {
                    if(response.data.success == true){
                        const TIME_COUNT = 60;
                        if (!that.timer) {
                            that.count = TIME_COUNT;
                            that.show = false;
                            that.timer = setInterval(() => {
                            if (that.count > 0 && that.count <= TIME_COUNT) {
                                that.count--;
                            } else {
                                that.show = true;
                                clearInterval(that.timer);
                                that.timer = null;
                                }
                            }, 1000)
						}
						Notify({
							message: '发送成功',
							duration: 2000,
							background: '#20D3B3'
						});
                    }else {
						that.timer = null;
						Notify({
							message: response.data.message + '(错误码: ' + response.data.code + ')',
							duration: 2000,
							background: '#FF6262'
						});
                    }                    
                })
                .catch(function (error) {
					 that.timer = null;
                });
            }
        },
		goRegister(ruleRegister) {      // 确认
				var that = this
				var Expression = /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{6,}$/;   
				var objExp=new RegExp(Expression);
				if(that.registerForm.mobile == '') {
					Notify({
						message: '请输入手机号码',
						duration: 2000,
						background: '#FF6262'
					});
					return false
				}
				if(that.registerForm.smsCode == '') {
					Notify({
						message: '请输入验证码',
						duration: 2000,
						background: '#FF6262'
					});
					return false
				}
				if(that.registerForm.passWord == '') {
					Notify({
						message: '请输入密码',
						duration: 2000,
						background: '#FF6262'
					});
					return false
				}
				if(that.registerForm.checkPass == '') {
					Notify({
						message: '请确认密码',
						duration: 2000,
						background: '#FF6262'
					});
					return false
				}
				if(that.registerForm.checkPass != that.registerForm.passWord) {
					Notify({
						message: '两次输入密码不一致!',
						duration: 2000,
						background: '#FF6262'
					});
					return false
				}
				if(!objExp.test(that.registerForm.checkPass) || !objExp.test(that.registerForm.passWord)) {
					Notify({
						message: '密码为不少于6位的数字和字母组成',
						duration: 2000,
						background: '#FF6262'
					});
					return false
				}
				that.axios(that.qdsGlobalApiUrl + 'notValidate/modifyPasswordbyMobile', {
					method: 'post',
					params: {
						mobile: that.registerForm.mobile ,
						smsCode: that.registerForm.smsCode,
						newPwd: that.registerForm.passWord
					}
				})
				.then(function (response) {
					if(response.data.code == "200"){
						Notify({
							message: '密码修改成功',
							duration: 2000,
							background: '#20D3B3'
						});
						setTimeout(function(){
							that.$router.push({path: '/'})
						},2000);
					}else {
						Notify({
							message: response.data.message + '(错误码: ' + response.data.code + ')',
							duration: 2000,
							background: '#FF6262'
						});
					}
				})
				.catch(function (error) {
				});
		},
    },
        
      
}

</script>

<style scoped>
	.post-head{
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        display: -webkit-flex;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 2.6875rem;
        border-bottom: 1px solid #F5F5F5;
        background-color: #fff;
    }
    .back{
        position: absolute;
        left: .9375rem;
        display: inline-block;
        width: .5625rem;
        height: 1.1875rem;
        background: url(../../assets/post/back_icon.png);
        background-size: 100% 100%;
        cursor: pointer;
    }
    .head-font{
        font-size: 1rem;
        color: #0B231F;
        font-weight: bold;
    }
	input {
		margin: 0;
		padding: 0;
		border: none;
	}
	img{
		vertical-align: middle;
		border: none;
	}
	.register-page {
		height: 100%;
		width: 100%;
		background: #fff;
	}
	.register-page .title-logo{
		height: 3.75rem;
	}
	.register-page .register-box {
		padding-top: .5rem;
		margin: 0 2rem;
	}
	.register-page .register-box .items-line {
		display: -webkit-flex;
		display: flex;
		flex-direction: row;
		align-items: center;
		justify-content: flex-start;
		height: 4rem;
		line-height: 4rem;
		border-bottom: 1px solid #E5E5E5;
	}
	.register-page .register-box .items-line img {
		width: .875rem;	
		padding-left: 20px;
	}

	.register-page .select-agreement{
		font-size: .75rem;
		padding: 1.4375rem 0 .6875rem 2.8125rem;
	}
	.register-page .select-agreement p input{
		display: none;
	}
	.register-page .select-agreement p .agreement_btn+label{
		display: inline-block;
		width: .8125rem;
		height: .8125rem;
		border: 1px solid gray;
		border-radius: 50%;
		
	}


	.register-page .login-btn-box {
		width: 80%;
		margin: 0 auto;
		text-align: center;
	}
	.register-page .login-btn-box span{
		display: inline-block;
		width: 100%;
		border-radius: 1.375rem;
		background: #2da1a4;
		line-height: 2.75rem;
		color: #fff;
		font-size: 1.0625rem;
	}
	.register-page .select-agreement .user-agreement{
		color: #2da1a4;
	}
	.zhanwei {
		height: 1.875rem;
	}
	.register-page .footer{
		letter-spacing: .1em;
		width:60% ;
		margin: 0 auto;
		display: -webkit-flex;
		display: flex;
		flex-direction: row;
		justify-content: space-around;
		font-size: .8125rem;
		color: #818D8D;
		font-size: .8125rem;
	}
	.register-page .footer .go-login-btn{
		color: #2da1a4;
	}
	.open-text {
		width: 80%;
	}

</style>



